import React, { Component } from 'react';

class Todos extends Component{
    handleSubmit = (e) => {
        e.preventDefault();
        const todo = { todo: document.forms[0][0].value ,id: Math.random()*10 };
        document.forms[0][0].value = '';
        this.props.addTodo(todo);
    }

    checked = (e) => {
        e.target.parentNode.previousSibling.style.textDecoration = 'line-through';
    }
    
    autoType = () => {
        let examples = document.getElementsByClassName('examples')[0];
        const todos = ['play ball', 'work out', 'take a hike', 'go to the beach', 'hangout with friends'];
        let i = 0, j = 0;

        function positive() {
            setTimeout(()=>{
                examples.innerHTML = todos[j].slice(0,i+1);
                i++;
                if(i >= todos[j].length){
                    setTimeout(()=>{
                        negetive();
                    }, 1000)
                } else {
                    positive();
                }
            },200)
        }

        function negetive() {
            setTimeout(()=>{
                examples.innerHTML = todos[j].slice(0,i-1);
                i--;
                if(i <= 0){
                    if(j+1 < todos.length){
                        j++;
                    } else {
                        j = 0;
                    }
                    positive();
                } else {
                    negetive();
                }
            },50)
        }
        positive();
    }

    handleClick = () => {
        setTimeout(()=>{
            this.autoType();
        },1000)
    }

    render(){
        const todoList = this.props.todos.length ? (
            this.props.todos.map(todo => {
                return(
                    <div className="todo border d-flex py-2 px-3" key={todo.id}>
                        <span className="montserrat text-decoration-cross">{todo.todo}</span>
                        <div className="ml-auto">
                            <i onClick={this.checked} class="fa fa-check text-info mr-3" aria-hidden="true"></i>
                            <i onClick={()=>{this.props.deleteTodo(todo.id)}} class="fa fa-trash text-danger" aria-hidden="true"></i>
                        </div>
                    </div>
                )
            })
        ) : (
            <div className="border p-3 text-center">
                <span><strong>You have no todos. Yay!!!</strong></span>
            </div>
        )

        return(
            <div className="col-lg-6 col-md-9 col-sm-12 m-auto">
                <div className="overflow-hidden rounded-lg card">
                    {todoList}
                </div>

                <div>
                    <form className="add my-4" onSubmit={this.handleSubmit}>
                        <input type="text" placeholder="Add a task" required minLength="3"/>
                        <button><i class="fa fa-plus" aria-hidden="true"></i></button>
                    </form>
                    <p className="text-disabled text-center">
                        E.g: <span className="examples bold"></span>
                    </p>
                </div>
            {this.handleClick()}
            </div>
        )
    }
}

export default Todos;