import React, {Component} from 'react';
import Header from './components/Header';
import Todos from './components/Todos';

class App extends Component {
  state = {
    todos: []
  }

  addTodo = (todo) => {
    const todoList = this.state.todos.map(todos=>{
      return todos;
    })

    todoList.push(todo);
    this.setState({ todos: todoList });
  }
  
  deleteTodo = (id) => {
    const todoList = this.state.todos.filter((todo)=>{
      return todo.id !== id;
    });
    
    this.setState({ todos: todoList });
  }

  render(){
    return (
      <div className="app container">
        <Header />
        <Todos todos={this.state.todos} addTodo={this.addTodo} deleteTodo={this.deleteTodo} />
      </div>
    )
  }
}

export default App;
